import Typography from 'components/Typography';
import Page from 'components/Page';
import React, {useState} from 'react';
import '../styles/pre.scss'


import {
  Card, CardBody, CardHeader, Col, Row
} from 'reactstrap';

function AuthPage()
{

  const [scope, setScope] = useState('');
  const onChange_Scope = (e) => {
    setScope(e.target.value);
  };

  const [client_id, setClient_id] = useState('');
  const onChange_Client_Id = (e) => {
    setClient_id(e.target.value);
  };

  const [client_secret, setClient_Secret] = useState('');
  const onChange_setClient_Secret = (e) => {
    setClient_Secret(e.target.value);
  };

  return (
    <Page title="Auth API">
      <Row>
        <Col>
          <Card>
            <CardHeader>설명</CardHeader>
            <CardBody>
              <Typography>
                API 토큰 생성하는 API를 제공합니다. <br/>
              </Typography>
            </CardBody>
          </Card>
        </Col>
      </Row>
      <Row>
        <Col>
          <Card>
            <CardHeader>API 토큰 생성 API</CardHeader>
            <CardBody>
              <Typography >
                POST방식 : https://warranty-service-dev.auth.ap-northeast-2.amazoncognito.com/oauth2/token<br/>
              </Typography>

              scope(범주) :  <input onChange={onChange_Scope} value={scope}/> <br/>
              client_id(API토큰 발급으로 발급 받은 ID) :  <input onChange={onChange_Client_Id} value={client_id}/> <br/>
              client_secret(PI토큰 발급으로 발급 받은 KEY) : <input onChange={onChange_setClient_Secret} value={client_secret}/> <br/>


              <pre className='precode'>
                async function getToken()<br/>
                &#123;<br/>
                &nbsp;const data = querystring.stringify(&#123;<br/>
                &nbsp;&nbsp;grant_type: 'client_credentials',<br/>
                &nbsp;&nbsp;scope: <p className='valuepre'>'{scope}'</p>,<br/>
                &nbsp;&nbsp;client_id: <p className='valuepre'>'{client_id}'</p>,<br/>
                &nbsp;&nbsp;client_secret: '<p className='valuepre'>'{client_secret}'</p><br/>
                &nbsp;&#125;);<br/>
                &nbsp;const options = &#123;<br/>
                &nbsp;&nbsp;hostname: 'warranty-service-dev.auth.ap-northeast-2.amazoncognito.com',<br/>
                &nbsp;&nbsp;port: 443,<br/>
                &nbsp;&nbsp;path: '/oauth2/token',<br/>
                &nbsp;&nbsp;method: 'POST',<br/>
                &nbsp;&nbsp;headers: &#123;<br/>
                &nbsp;&nbsp;&nbsp;&nbsp;'Content-length':data.length,<br/>
                &nbsp;&nbsp;&nbsp;&nbsp;"Content-Type": 'application/x-www-form-urlencoded'<br/>
                &nbsp;&nbsp;&nbsp;&#125;<br/>
                &nbsp;&#125;;<br/>
                &nbsp;return await commonModel.httpRequest(options,data)<br/>
                &#125;<br/>
              </pre>

              <p className='ResultValue'> 결과 값 : access_token KEY 발급</p><br/>

            </CardBody>
          </Card>
        </Col>
      </Row>
    </Page>
  )
};

export default AuthPage;
